body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  color: rgb(243, 243, 245);
}

nav {
  position: fixed;
  display: flex;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  padding: 0;
  /* background: transparent; */
  background: linear-gradient(90deg, rgb(15, 15, 15) 28%, rgb(31, 30, 31) 46%, rgb(98, 98, 100) 98%);
  align-items: center;
  font-size: 20px;
  justify-content: space-between;
}

.logo-container {
  position: relative;
  font-size: 30px;
  text-decoration: none;
  font-weight: 1000;
  align-items: center;
  margin-left: 2%;
}

.link-container {
  display: flex;
  justify-content: space-between;
  width: 60%;
  font-weight: 600;
  height: 40px;
  align-items: center;
  margin-right: 2%;
}

hr {
  margin: 10vh 15vh;
  border-color: blueviolet;
}


.link-button:hover {
  cursor: pointer;
  font-weight: 1000;
}

.header-icon {
  margin-right: 6px;
}

.main-container {
  position: static;
  width: 100%;
  background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(121,9,120,1) 46%, rgba(98,0,255,1) 98%);
  background-position: center;
  padding-top: 60px;
}

.home-container {
  height: 80vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.welcome-text-container {
  display: flex;
  justify-content: space-between;
  padding: 5% 9%;
  height: 70%;
  align-items: center;
}

.text-container {
  color: black;
}

.welcome-text-container img {
  padding: 8vh 10vh;
  width: 38vh;
  height: 30vh;
  border-radius: 60%;
  box-shadow: 1px 20px 20px 1px rgb(41, 2, 46);
}

.name-title {
  font-size: 6vw;
}

.contact-container {
  display: flex;
  justify-content: space-around;
  padding: 0% 10%;
  align-items: center;
  font-size: 1.5vw;
  font-weight: 600;
}

.github-container {
  font-weight: bold;
  border-radius: 50px;
}

.linkedin-container {
  font-weight: bold;
  border-radius: 40px;
}

.contact-icon {
  background-color: rgb(0, 7, 7);
}

.button-contact {
  font-size: 5vw;
}

.about-container {
  margin: 15% 20%;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 60%;
  box-shadow: 1px 3px 1px 8px rgb(41, 2, 46);
}

.about-container h2 {
  font-size: 10vh;
  color: black;
  padding: 1vh;
}

.about-container .paragrapfh {
  padding: 2vh;
  color: black;
  font-size: 3vh;
}

.about-container a {
  text-decoration: none;
  font-size: 4vh;
  color: rgb(15, 1, 15);
}

.about-container a:hover {
  color: rgb(75, 3, 42);
}


.img-skillcard {
  width: 9vh;
  height: 9vh;
}

.main-div-skills {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.main-title {
  margin-bottom: 20px;
  font-size: 12vh;
  color: black;
}

.skill-cards {
  display: flex;
  justify-content: center;
  /* overflow-x: scroll; */
  width: 12vh;
  padding: 8vh;
  border-radius: 10%;
  box-shadow: 1px 20px 20px 1px rgb(41, 2, 46);
}

.title-card-skill {
  margin-top: 30px;
  align-items: center;
  color: black;
}

.skill-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20%;
  width: 100%;
  margin: 10%;
}

.skillcard-title {
  margin-top: 10%;
  margin-bottom: 10%;
  font-size: 1.5vh;
}

.skill-button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  padding: 9vh;
  font-size: 200%;
  margin: 0 10px;
}

.main-div-project {
  text-align: center;
}


.project-cards {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
}

.project-button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  padding: 4vh;
  font-size: 200%;
  margin: 0 10px;
}

.project-card {
  display: flex;
  justify-content: center;
  /* overflow-x: scroll; */
  width: 14vh;
  padding: 10vh;
  border-radius: 20%;
  box-shadow: 1px 20px 20px 1px rgb(41, 2, 46);
}

.footer {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  /* border: solid; */
  margin-top: 20%;
  position: static;
  background: linear-gradient(90deg, rgb(15, 15, 15) 28%, rgb(31, 30, 31) 46%, rgb(98, 98, 100) 98%);
}

.footer-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 2% 2%;

}

.footer-icon {
  align-items: center;
}

.contact-icon:hover {
  animation: bounce 1s 0s forwards
}

.name-title {
  animation: transition 5s infinite alternate-reverse;
}


@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {transform: translateY(0);} 
  40% {transform: translateY(-30px);} 
  60% {transform: translateY(-15px);} 
}

@keyframes transition {
  0% {
    color: rgb(0, 0, 0);
  }
  25% {
    color: rgb(41, 40, 40);
  }
  50% {
    color: rgb(83, 82, 83);
  }
  75% {
    color: rgb(133, 133, 139);
  }
  100% {
    color: rgb(218, 217, 224);
  }
}


@media screen and (max-width: 768px) {

  .link-container {
    width: 80%;
    margin: 0% 15%;
  }
  .link-button {
    font-size: 40%;
    margin-right: 1%;
  }
  .logo-container {
    font-size: 0%;
  }
  .name-title {
    font-size: 8vw;
  }
  .contact-icon{
    font-size: 10vh;
  }
  .about-container {
    width: 60%;
    /* font-size: 2vh; */
  }

  .about-container h2 {
    font-size: 4vh;
  }
  .welcome-text-container img {
    padding: 1vh 1%;
    margin: 5%;
  }
  .main-div-project {
    size: 100%;
  }
  .main-title {
    font-size: 10vh;
    padding: 2%;
  }
  .project-cards {
    font-size: 1vh;
    padding: 1vh 50%;
  }
  .footer {
    font-size: 1vh;
    padding: 1vh 5%;
  }
}